import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import { Link, useNavigate } from 'react-router-dom'
import { useState, useEffect, useContext } from "react";
import questContext from '../context/questContext'



export default function QuestionNumber(){

    const navigate = useNavigate()

    const {questionTotal, setQuestionTotal, level, setLevel, primaryQuestion} = useContext(questContext);
   

    const handleQuestionTotal = (value) => {
        setQuestionTotal(value);
    };
    const handleLevel = (value) => {
        setLevel(value);
    };

    return (
        <div>
        
            <h1>HSK VOCABULARY TEST</h1>

            <div>
                <Button variant={questionTotal === 10 ? 'primary' : 'secondary'} onClick={() => handleQuestionTotal(10)}>
                    10
                </Button>
                <Button variant={questionTotal === 20 ? 'primary' : 'secondary'} onClick={() => handleQuestionTotal(20)}>
                    20
                </Button>
                <Button variant={questionTotal === 50 ? 'primary' : 'secondary'} onClick={() => handleQuestionTotal(50)}>
                    50
                </Button>
            </div>


            <div>
                <Button variant={level === 1 ? 'primary' : 'secondary'} onClick={() => handleLevel(1)}>
                    HSK 1
                </Button>
                <Button variant={level === 2 ? 'primary' : 'secondary'} onClick={() => handleLevel(2)}>
                    HSK 2
                </Button>
                <Button variant={level === 3 ? 'primary' : 'secondary'} onClick={() => handleLevel(3)}>
                    HSK 3
                </Button>
                <Button variant={level === 4 ? 'primary' : 'secondary'} onClick={() => handleLevel(4)}>
                    HSK 4
                </Button>
                <Button variant={level === 5 ? 'primary' : 'secondary'} onClick={() => handleLevel(5)}>
                    HSK 5
                </Button>
                <Button variant={level === 6 ? 'primary' : 'secondary'} onClick={() => handleLevel(6)}>
                    HSK 6
                </Button>
            </div>


            <div>
            <Button onClick={() => {navigate("/questionTest")}} variant="secondary">start</Button>
            {/* <Button onClick={() => {newStart(10)}} variant="secondary">10</Button>
            <Button onClick={() => {newStart(20)}} variant="secondary">20</Button>
            <Button onClick={() => {newStart(50)}} variant="secondary">50</Button> */}
            </div>
        </div>
    )
}


