import { useState } from 'react';
import { getSavedList } from '../utils/utils';
import { Link, useNavigate } from 'react-router-dom'

import { 
  Flex, Spacer, Divider, Box,
  Heading, Text, 
  Button, 
  Card, CardHeader, CardBody, IconButton,
  AbsoluteCenter
} from '@chakra-ui/react'

import {
  Modal, ModalOverlay, ModalContent, ModalHeader, ModalFooter, ModalBody, ModalCloseButton,
} from '@chakra-ui/react'

import { ArrowBackIcon, CloseIcon } from '@chakra-ui/icons'



function SavedList() {
  const [list, setList] = useState(getSavedList());
  const [show, setShow] = useState(false);
  const [itemIndex, setItemIndex] = useState(null);

  const navigate = useNavigate()

  const handleClose = () => {
    setShow(false);
    setItemIndex(null);
  };

  const handleShow = (index) => {
    setShow(true);
    setItemIndex(index);
  };

  const discardItem = () => {
    if (itemIndex !== null) {
      const updatedList = [...list];
      updatedList.splice(itemIndex, 1);

      // Update the state with the new list
      setList(updatedList);

      // Update the local storage
      localStorage.setItem('savedList', JSON.stringify(updatedList));

      handleClose();
    }
  };

  return (
    <Flex height="100vh" width="100vw"  flexDirection='column' >
      

      <Flex flexDirection='column' justify="center" bg='teal' borderBottomRadius='30px'> 
        
        <Flex justify="flex-start" align="center" width="100%" p={8}>
          <IconButton ml={-4} boxSize={0} fontSize='24px' aria-label="Go back" icon={<ArrowBackIcon />} onClick={()=>{navigate('/')}}/>

          <AbsoluteCenter axis='horizontal' align='center'>
            <Text m='auto' textStyle='heading'>Your Result</Text>
          </AbsoluteCenter>
        </Flex>

        <Flex flexDirection={'row'}  justify="center" mt={-2} pb={4}>
          <Text fontSize='6xl'>{list.length}</Text>
          <Text fontSize='md' mt={12}>Cards Saved</Text>
        </Flex>
        
      </Flex>

      <Flex justify="center" position="relative" zIndex="1">
        <Button onClick={()=>{navigate('/listTest')}} size='md' position="relative" top="-50%" zIndex="2" >
          Test Me!
        </Button>
      </Flex>
     
      {list.map((item, index) => (
      <Flex align='center' flexDirection='column'>
       
        <Card key={index} variant='outline' borderColor='teal' w='90%' mb={5}>
          <Flex align="center" direction='row' mr='5'>
            <Box p={5} py={2} minW='120px'  >
              <Text color='black' textStyle='hanziText' mx={0} mt={3} >{item.hanzi}</Text>
              <Text color='black' textStyle='pinyinText' mx={0} mt={-4}>{item.pinyin}</Text>
            </Box>

            <Divider orientation="vertical" borderColor="gray.500" height="80%" pr={2}/>

            <Box p={4} >
              <Text color='black' fontSize='18px' textStyle='translationText'  m='0'>{item.translation}</Text>
            </Box>

            <Spacer/> {/* Untuk mendorong button cancel ke kanan */}

            <IconButton boxSize={0} icon={<CloseIcon/>} onClick={() => handleShow(index)}/>

          </Flex>

        </Card>

        <div>
          <Modal isOpen={show && itemIndex === index} onClose={handleClose}>
            <ModalOverlay />
            <ModalContent maxW='90%'>
              <ModalHeader>Deleting Items</ModalHeader>
              <ModalCloseButton />
              <ModalBody>
                  Are You Sure Want to Discard this card?
              </ModalBody>

              <ModalFooter>
                <Button colorScheme='blue' mr={3} onClick={handleClose}>
                  Cancel
                </Button>
                <Button variant='ghost' onClick={discardItem}>Yes</Button>
              </ModalFooter>
            </ModalContent>
          </Modal>
        </div>
       
      </Flex>
        ))}
    </Flex>
  );
}

export default SavedList;
