import { useState, useEffect, useContext } from "react";
import { useQuery } from "react-query";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import MultipleChoice from "../components/multipleChoice";
import ScoreTest from "../components/scoreTest"
import UserAnswer from "../components/userAnswer"
import { Link, useNavigate } from 'react-router-dom'
import questContext from '../context/questContext'


export default function ResultTest() {
  
  const navigate = useNavigate()

  const {countCorrect, userAnswer, setUserAnswer, setPrimaryQuestion, setCountCorrect} = useContext(questContext);


  function goToListPage(){
    navigate('/listPage')
    setUserAnswer([])
    setPrimaryQuestion([])
    setCountCorrect(0)
  }

  return (
    <div>
        {/* <Button onClick={()=>{goToListPage()}}>
          Saved List
        </Button> */}
        <ScoreTest/>
        <UserAnswer/>

        
    </div>
  );
}
