import { useState, useEffect } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import './App.css';
import Homepage from './pages/homepage'
import QuestionMenu from './pages/questionMenu'
import QuestionTest from './pages/questionTest'
import ResultTest from './pages/resultTest'
import ListPage from './pages/listPage'
import ListTest from './pages/listTest'

import questContext from './context/questContext'



function App() {

  const [questionTotal, setQuestionTotal] = useState(20); // Set the default selected button value
  const [level, setLevel] = useState(5); // Set the default selected button value
  const [userAnswer, setUserAnswer] = useState([]);
  const [countCorrect, setCountCorrect] = useState(0);
  const [primaryQuestion, setPrimaryQuestion] = useState([]);

  return (

    

      <questContext.Provider value={{ questionTotal, setQuestionTotal, level, setLevel, userAnswer, setUserAnswer, countCorrect, setCountCorrect, primaryQuestion, setPrimaryQuestion}}>
      
        <Routes>
          <Route exact path="/" element={<Homepage/>}/>
          <Route path="/questionMenu" element={<QuestionMenu/>}/>
          <Route path="/questionTest" element={<QuestionTest/>}/>
          <Route path="/resultTest" element={<ResultTest/>}/>
          <Route path="/listPage" element={<ListPage/>}/>
          <Route path="/listTest" element={<ListTest/>}/>
        </Routes>
  
      </questContext.Provider>


  );
}

export default App;
