import { Link, useNavigate } from 'react-router-dom'
import { useState, useEffect, useContext } from "react";
import { useQuery } from "react-query";
// import Button from "react-bootstrap/Button";
// import Card from "react-bootstrap/Card";
// import ButtonGroup from "react-bootstrap/ButtonGroup";
import questContext from '../context/questContext'


import { 
  Flex, Spacer, Divider, Box,
  Heading, Text, 
  Button, 
  Card, CardHeader, CardBody, IconButton,
  AbsoluteCenter
} from '@chakra-ui/react'

import {
  Modal, ModalOverlay, ModalContent, ModalHeader, ModalFooter, ModalBody, ModalCloseButton,
} from '@chakra-ui/react'

import { ArrowBackIcon, CloseIcon } from '@chakra-ui/icons'


function QuestionGenerate() {

    const navigate = useNavigate()

    const {questionTotal, level, userAnswer, setUserAnswer, primaryQuestion, setPrimaryQuestion, setCountCorrect} = useContext(questContext)

    const [generateChoice, setGenerateChoice] = useState([]);
    const [divOrder, setDivOrder] = useState([]);
    const [isDone, setIsDone] = useState(false);
 
    const { data, isLoading, isError } = useQuery("todo", () =>
    fetch(
      `https://raw.githubusercontent.com/clem109/hsk-vocabulary/master/hsk-vocab-json/hsk-level-${level}.json`
    ).then((res) => res.json()),
  {
    onSuccess: () => {
      setPrimaryQuestion([]);
      generateRandomNumber();
    },
  }
  );

  function generateNumberLevel(level) {
    switch (level) {
      case 1:
        return Math.floor(Math.random() * 150);
      case 2:
        return Math.floor(Math.random() * 149);
      case 3:
        return Math.floor(Math.random() * 298); 
      case 4:
        return Math.floor(Math.random() * 600); 
      case 5:
        return Math.floor(Math.random() * 1299); 
      case 6:
        return Math.floor(Math.random() * 2499); 
      default:
        return 0; // Default case, you may want to handle this differently based on your requirements
    }
  }
      
  function generateRandomNumber() {
    // setGenerateChoice([]);
    const randomNumberForQuestion = [];
    const randomNumberForOrder = [];

    for (let i = 0; i < 5; ) {
      
      let randomNumber = generateNumberLevel(level)
      if (!randomNumberForQuestion.includes(randomNumber)) {
        randomNumberForQuestion.push(randomNumber);
        i++;
      }
    }

    for (let i = 0; i < 7; ) {
      let randomNumber = Math.floor(Math.random() * 7);
      if (!randomNumberForOrder.includes(randomNumber)) {
        randomNumberForOrder.push(randomNumber);
        i++;
      }
    }

    

    setDivOrder(randomNumberForOrder);
    setGenerateChoice(randomNumberForQuestion);
  }


  function nextQuestion(isCorrect, userInput) {

    if (isCorrect==="true") {
      setCountCorrect((previous) => previous + 1);
      generateRandomNumber();
    
      // setPrimaryQuestion(primaryQuestion => [...primaryQuestion, generateChoice[0]])
      setPrimaryQuestion((prevQuestions) => [
        ...prevQuestions,
        { userAnswer:userInput, id:data[generateChoice[0]]?.id, hanzi: data[generateChoice[0]]?.hanzi, pinyin: data[generateChoice[0]]?.pinyin, translation: data[generateChoice[0]]?.translations[0], isCorrect: true },
      ]);      
      setUserAnswer(userAnswer => [...userAnswer, userInput])
      
      
      
    } else if (isCorrect==="false"){
      generateRandomNumber();
     
      // setPrimaryQuestion(primaryQuestion => [...primaryQuestion, generateChoice[0]])
      setPrimaryQuestion((prevQuestions) => [
        ...prevQuestions,
        { userAnswer:userInput, id:data[generateChoice[0]]?.id, hanzi: data[generateChoice[0]]?.hanzi, pinyin: data[generateChoice[0]]?.pinyin, translation: data[generateChoice[0]]?.translations[0], isCorrect: false },
      ]);
      setUserAnswer(userAnswer => [...userAnswer, userInput])
  
    } 
    
  
  }

  useEffect(() => {

    if (userAnswer.length === questionTotal) {
     
      setIsDone(true)
      // Wait for 2 seconds before navigating
      const timeoutId = setTimeout(() => {
        navigate('/resultTest');
      }, 2000);
  
      // Cleanup the timeout to avoid memory leaks
      return () => clearTimeout(timeoutId);
    }

 
    }, [userAnswer]);

  useEffect(() => {
    setPrimaryQuestion([])
    generateRandomNumber();
    setIsDone(false)
    setUserAnswer([])
  }, [data]); // Add an empty dependency array here to run on first render


  
  if (isLoading) return <div>Loading...</div>;
  
  if (isError) return <div>Error fetching data</div>;
  

    return (
      <div style={{ display: isDone ? 'none' : 'block' }}>

    <Flex height="100vh" width="100vw"  flexDirection='column' >
      

      <Flex flexDirection='column' justify="center"> 
        
        <Flex justify="flex-start" align="center" width="100%" p={8}>
          <IconButton ml={-4} boxSize={0} fontSize='24px' aria-label="Go back" icon={<ArrowBackIcon />} onClick={()=>{navigate('/')}}/>

          <AbsoluteCenter axis='horizontal' align='center'>
            <Text m='auto' textStyle='heading'>Questions</Text>
          </AbsoluteCenter>
        </Flex>
        
      </Flex>

      {/* Progress Bar */}

      <Flex justify='center'>
        <Text textStyle='questionText'>{data && data[generateChoice[0]]?.hanzi}</Text>
      </Flex>
     
    
      <Flex align='center' flexDirection='column'>
       
      <Box style={{ order: divOrder[2] }} align='center' w='16rem' h='auto' maxHeight='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("true",data[generateChoice[0]]?.translations[0])}}>
        {data && data[generateChoice[0]]?.translations[0]}
      </Box>

      <Box style={{ order: divOrder[3]}} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[1]]?.translations[0])}}>
        {data && data[generateChoice[1]]?.translations[0]}
      </Box>

      <Box style={{ order: divOrder[4] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[2]]?.translations[0])}}>
        {data && data[generateChoice[2]]?.translations[0]}
      </Box>

      <Box style={{ order: divOrder[5] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[3]]?.translations[0])}}>
        {data && data[generateChoice[3]]?.translations[0]}
      </Box>

      <Box style={{ order: divOrder[6] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[4]]?.translations[0])}}>
        {data && data[generateChoice[4]]?.translations[0]}
      </Box>

      </Flex>

      
    </Flex>

      
      {/* <div className="container" style={{ backgroundColor: '#D8FFDB' }}>
      
        <div style={{ 'textAlign': "center",  }}>
            <div>
          
            <h2 >{data && data[generateChoice[0]]?.hanzi}</h2>
            </div>

            <div className="multipleChoice">

              <div className="buttonChoice" style={{ order: divOrder[2] }} variant="light"
                  onClick={() => {nextQuestion("true",data[generateChoice[0]]?.translations[0])}}
              >{data && data[generateChoice[0]]?.translations[0]}
              </div>

              <div className="buttonChoice" style={{ order: divOrder[3] }} variant="light"
                  onClick={() => {nextQuestion("false",data[generateChoice[1]]?.translations[0])}}
              >{data && data[generateChoice[1]]?.translations[0]}
              </div>

              <div className="buttonChoice" style={{ order: divOrder[4] }} variant="light"
                  onClick={() => {nextQuestion("false",data[generateChoice[2]]?.translations[0])}}
              >{data && data[generateChoice[2]]?.translations[0]}
              </div>

              <div className="buttonChoice" style={{ order: divOrder[5] }} variant="light"
                  onClick={() => {nextQuestion("false",data[generateChoice[3]]?.translations[0])}}
              >{data && data[generateChoice[3]]?.translations[0]}
              </div>

              <div className="buttonChoice" style={{ order: divOrder[6] }} variant="light"
                  onClick={() => {nextQuestion("false",data[generateChoice[4]]?.translations[0])}}
              >{data && data[generateChoice[4]]?.translations[0]}
              </div>

            </div>
        </div>
    </div> */}
    </div>
    );
  }
  
  export default QuestionGenerate;