import { useState, useEffect, useContext, useRef } from "react";
// import Button from 'react-bootstrap/Button';
import { Link, useNavigate } from 'react-router-dom'
import questContext from '../context/questContext'
import { getSavedList } from '../utils/utils';
import { useQuery } from "react-query";


import { 
  Flex, Spacer, Divider, Box,
  Heading, Text, 
  Button, 
  Card, CardHeader, CardBody, IconButton,
  AbsoluteCenter
} from '@chakra-ui/react'

import {
  Modal, ModalOverlay, ModalContent, ModalHeader, ModalFooter, ModalBody, ModalCloseButton,
} from '@chakra-ui/react'

import { ArrowBackIcon, CloseIcon } from '@chakra-ui/icons'


function ListQuestion() {

    const [list, setList] = useState(getSavedList());

    const saveQuestion = useRef([]);

    const [generateChoice, setGenerateChoice] = useState([]);
    const [divOrder, setDivOrder] = useState([]);
    // const [saveQuestion, setSaveQuestion] = useState([]); //prevent the same question
    const [isDone, setIsDone] = useState(false);
    const {questionTotal, level, userAnswer, setUserAnswer, primaryQuestion, setPrimaryQuestion, setCountCorrect} = useContext(questContext)

    const { data, isLoading, isError } = useQuery("todo", () =>
      fetch(
        `https://raw.githubusercontent.com/clem109/hsk-vocabulary/master/hsk-vocab-json/hsk-level-${level}.json`
      ).then((res) => res.json()),
    {
      onSuccess: () => {
        setPrimaryQuestion([]);
        generateRandomNumber();
      },
    }
    );

    

    const navigate = useNavigate()

    useEffect(() => {
      console.log("saveQuestion.length:", saveQuestion.current);
      console.log("list.length:", list.length);
  
      if (saveQuestion.current.length === list.length) {
          console.log("DONE");
          console.log("saveQuestion", saveQuestion.current);
          console.log("list", list);
      }
  }, [userAnswer]);

    function generateRandomNumber(exclude){
      // console.log("generate random...");
        const randomNumberForQuestion = [];
        const randomNumberForOrder = [];
        
    
        for (let i = 0; i < 5; ) {
            let randomIndex = Math.floor(Math.random() * list.length);
         
            if (!randomNumberForQuestion.includes(randomIndex)) {
              randomNumberForQuestion.push(randomIndex);
              i++;
            }
          }

        // {console.log("randomNumberForQuestion",randomNumberForQuestion)}

        
        // {console.log("randomNumberForQuestion",randomNumberForQuestion)}

      // {console.log("randomNumberForQuestion",randomNumberForQuestion)}
      // {console.log("primaryQuestion",primaryQuestion)}


      
        for (let i = 0; i < 7; ) {
          let randomNumber = Math.floor(Math.random() * 7);
          if (!randomNumberForOrder.includes(randomNumber)) {
            randomNumberForOrder.push(randomNumber);
            i++;
          }
        }

        setDivOrder(randomNumberForOrder);
        setGenerateChoice(randomNumberForQuestion);
    }

    function nextQuestion(isCorrect, userInput) {
      // console.log("Next question clicked...");
      if (isCorrect==="true") {
        setCountCorrect((previous) => previous + 1);
        generateRandomNumber();
      
        // setPrimaryQuestion(primaryQuestion => [...primaryQuestion, generateChoice[0]])
        setPrimaryQuestion((prevQuestions) => [
          ...prevQuestions,
          { userAnswer:userInput, id:list[generateChoice[0]]?.id, hanzi: list[generateChoice[0]]?.hanzi, pinyin: list[generateChoice[0]]?.pinyin, translation: list[generateChoice[0]]?.translation, isCorrect: true },
        ]);      
        setUserAnswer(userAnswer => [...userAnswer, userInput])
        
        
        
      } else if (isCorrect==="false"){
        generateRandomNumber();
       
        // setPrimaryQuestion(primaryQuestion => [...primaryQuestion, generateChoice[0]])
        setPrimaryQuestion((prevQuestions) => [
          ...prevQuestions,
          { userAnswer:userInput, id:list[generateChoice[0]]?.id, hanzi: list[generateChoice[0]]?.hanzi, pinyin: list[generateChoice[0]]?.pinyin, translation: list[generateChoice[0]]?.translation, isCorrect: false },
        ]);
        setUserAnswer(userAnswer => [...userAnswer, userInput])
    
      } 
      
    
    }
   

      useEffect(() => {

        if (userAnswer.length === list.length) {
         
          setIsDone(true)
          // Wait for 2 seconds before navigating
          const timeoutId = setTimeout(() => {
            navigate('/resultTest');
          }, 2000);
      
          // Cleanup the timeout to avoid memory leaks
          return () => clearTimeout(timeoutId);
        }
    
     
        }, [userAnswer]);

      
    
        


    useEffect(() => {
        setPrimaryQuestion([])
        generateRandomNumber();
        setIsDone(false)
        setUserAnswer([])
        saveQuestion.current = []
      }, []);


    return (
        
      <div style={{ display: isDone ? 'none' : 'block' }}>
   

            <Flex height="100vh" width="100vw"  flexDirection='column' >
              

              <Flex flexDirection='column' justify="center"> 
                
                <Flex justify="flex-start" align="center" width="100%" p={8}>
                  <IconButton ml={-4} boxSize={0} fontSize='24px' aria-label="Go back" icon={<ArrowBackIcon />} onClick={()=>{navigate('/')}}/>

                  <AbsoluteCenter axis='horizontal' align='center'>
                    <Text m='auto' textStyle='heading'>Questions</Text>
                  </AbsoluteCenter>
                </Flex>
                
              </Flex>

              {/* Progress Bar */}

              <Flex justify='center'>
                <Text textStyle='questionText'>{list && list[generateChoice[0]]?.hanzi}</Text>
              </Flex>
            
            
              <Flex align='center' flexDirection='column'>
              
              <Box style={{ order: divOrder[2] }} align='center' w='16rem' h='auto' maxHeight='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("true",list[generateChoice[0]]?.translation)}}>
                {list && list[generateChoice[0]]?.translation}
              </Box>

              <Box style={{ order: divOrder[3]}} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",list[generateChoice[1]]?.translation)}}>
                {list && list[generateChoice[1]]?.translation}
              </Box>

              <Box style={{ order: divOrder[4] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[0]]?.translations[0])}}>
                {data && data[generateChoice[0]]?.translations[0]}
              </Box>

              <Box style={{ order: divOrder[5] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[1]]?.translations[0])}}>
                {data && data[generateChoice[1]]?.translations[0]}
              </Box>

              <Box style={{ order: divOrder[6] }} align='center' w='16rem' h='auto' mx='auto' px={10} py={5} mb={8} borderWidth='1px' borderRadius='lg' borderColor='teal' color='teal' onClick={() => {nextQuestion("false",data[generateChoice[2]]?.translations[0])}}>
                {data && data[generateChoice[2]]?.translations[0]}
              </Box>

              </Flex>

              
            </Flex>

              
            
           </div>
        // </div>
    );
  }
  
  export default ListQuestion;