import { useState, useEffect,useContext} from "react";
import { useQuery } from "react-query";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import MultipleChoice from "../components/multipleChoice";
import QuestionGenerate from "../components/questionGenerate"
import questContext from '../context/questContext'
import { Link, useNavigate } from 'react-router-dom'


export default function ScoreTest() {

  const navigate = useNavigate()


  const {countCorrect, userAnswer, setUserAnswer, setPrimaryQuestion, setCountCorrect} = useContext(questContext);

  function restart(){
    navigate('/questionMenu')
    setUserAnswer([])
    setPrimaryQuestion([])
    setCountCorrect(0)
  }

  return (
    <div className="container">
        {/* <Card>
          <Card.Body>
              <Card.Title>Your Score</Card.Title>
              <Card.Text>
              {countCorrect}/{userAnswer.length}
              </Card.Text>
              <Button onClick={restart} variant="primary">
              Restart
              </Button>
          </Card.Body>
        </Card> */}
    </div>
  );
}