import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import { Link, useNavigate } from 'react-router-dom'
import SavedList from '../components/savedList'
import { getSavedList } from '../utils/utils';
import Alert from 'react-bootstrap/Alert';



function ListPage() {
    const [list, setList] = useState(getSavedList());
    const [showAlert, setShowAlert] = useState(false);
    const navigate = useNavigate()


    const checkIfTen = () => {
      if (list.length < 10) {
        // Show the alert
        setShowAlert(true);
  
        // Hide the alert after a short delay (e.g., 3 seconds)
        setTimeout(() => {
          setShowAlert(false);
        console.log(list)
      }, 3000); // Adjust the delay as needed
      } else {
      console.log(list)
        // Navigate to another page
        navigate('/listTest');
      }
    };
  

    return (
      <div>
       {/* <Button variant='primary' onClick={() => navigate('/')}>
            Back to Home
        </Button> */}
        <SavedList/>
        {/* <Button variant='primary' onClick={() => checkIfTen()}>
            Let Me Test
        </Button>

        <Alert variant='warning' show={showAlert}>
          Try to save more than 10 List. Then you can test your knowledge!
        </Alert> */}
     
      </div>
    );
  }
  
  export default ListPage;