// import Button from 'react-bootstrap/Button';
import { Link, useNavigate } from 'react-router-dom'
import { Button, ButtonGroup } from '@chakra-ui/react'


function Homepage() {

    const navigate = useNavigate()

    return (
      <div style={{ backgroundColor: '#D8FFDB' }}>
        <Button variant="primary" onClick={()=>{navigate('/questionMenu')}}>Vocab Test</Button>
        <Button variant="primary">Memory Card</Button>
        <Button variant="primary">Flash Card</Button>
        <Button variant="primary" onClick={()=>{navigate('/listPage')}}>Saved List</Button>
        
      </div>
    );
  }
  
  export default Homepage;