import { Link, useNavigate } from 'react-router-dom'



export default function UserAnswer() {
  
    const { countCorrect, primaryQuestion, userAnswer, setUserAnswer, setPrimaryQuestion, setCountCorrect } = useContext(questContext);
    const navigate = useNavigate()

  // Define a function to handle saving to the list
    const saveToList = (item) => {
        // Retrieve the existing list from local storage or initialize an empty array
        const existingList = getSavedList();


        // Check if the existing list already contains an item with the same ID
        const isItemAlreadyInList = existingList.some(existingItem => existingItem.id === item.id);

        if (!isItemAlreadyInList) {
            // Add the current item to the list
            const updatedList = [...existingList, item];

            // Save the updated list back to local storage
            localStorage.setItem("savedList", JSON.stringify(updatedList));
        } 
    };

    function navigateTo(destination){

        setUserAnswer([])
        setPrimaryQuestion([])
        setCountCorrect(0)

        switch (destination){
            case 'home':
                navigate('/')
                break;
            case 'listPage':
                navigate('/listPage')
                break;
            case 'questionMenu':
                navigate('/questionMenu')
                break;
            default:
                navigate('/')
        }
       
        
    }


    console.log(userAnswer)
    return (
    
            <Flex height="100vh" width="100vw"  flexDirection='column' >

                <Flex flexDirection='column' justify="center" bg='teal' borderBottomRadius='30px'> 
                    
                    <Flex justify="flex-start" align="center" width="100%" p={8}>
                    <IconButton color='white' ml={-4} boxSize={0} fontSize='24px' aria-label="Go back" icon={<ArrowBackIcon />} onClick={()=>{navigateTo('home')}}/>

                    <AbsoluteCenter axis='horizontal' align='center'>
                        <Text m='auto' textStyle='heading' color='white'>Your Result</Text>
                    </AbsoluteCenter>
                    </Flex>

                    <Flex flexDirection={'row'}  justify="center" mt={-2} pb={4}>
                    <Text fontSize='6xl' color='white'>{countCorrect}</Text>
                    <Text fontSize='md' color='white' mt={12}> out of {userAnswer.length}</Text>
                    </Flex>
                    
                </Flex>

                <Flex justify="center" gap={5} flexDirection='row' position="relative" zIndex="1" >
                    <Button onClick={()=>{navigateTo('listPage')}} size='md' position="relative" top="-50%" zIndex="2" >
                        Saved List
                    </Button>
                    <Button onClick={()=>{navigateTo('questionMenu')}} size='md' position="relative" top="-50%" zIndex="2" >
                        Restart
                    </Button>
                </Flex>
                
                {primaryQuestion.map((item, index) => (
                <Flex align='center' flexDirection='column' >
                
                    <Card key={index} variant='outline' w='90%' mb={5} bg={item.isCorrect?'green.600':'orange.500'}>
                        <Flex align="center" direction='row' mr='2'>
                            <Box p={5} py={2} minW='120px'  >
                            <Text color='white' textStyle='hanziText' mx={0} mt={3} >{item.hanzi}</Text>
                            <Text color='white' textStyle='pinyinText' mx={0} mt={-4}>{item.pinyin}</Text>
                            </Box>

                            <Divider orientation="vertical" borderColor="gray.100" height="80%" pr={2}/>

                            <Flex p={4} flexDirection='column' gap={2}>
                                <div style={{display:item.isCorrect?'none':'block'}}>
                                <Flex flexDirection='row' align='center' gap={2} >
                                    <CloseIcon boxSize={3}/>
                                    <Text color='white' textStyle='translationText' m='0'>{item.userAnswer}</Text>
                                </Flex>
                                </div>
                                <Flex flexDirection='row' align='center' gap={2}>
                                    <CheckCircleIcon boxSize={4}/>
                                    <Text color='white' textStyle='translationText'  m='0'>{item.translation}</Text>
                                </Flex>
                            </Flex>

                            <Spacer/> {/* Untuk mendorong button cancel ke kanan */}

                            <IconButton boxSize={0} icon={<AddIcon/>} onClick={() =>{saveToList(item)}}/>

                        </Flex>

                    </Card>

                    
                
                </Flex>
                    ))}
                     {/* <div className="container">
            <div style={{ maxHeight: '500px', overflowY: 'auto' }}>
                {primaryQuestion.map((item, index) => (
                    <div>
                    <Card key={index} style={{ width: '18rem' }}>
                        <Card.Body>
                            <Card.Text >Question: {item.hanzi}</Card.Text>
                            <Card.Text >Correct Answer: {item.translation}</Card.Text>
                            <Card.Text >Your Answer: {userAnswer[index]}</Card.Text>
                            <Card.Text >Correct?: {item.isCorrect}</Card.Text>
                            <Button variant="primary" onClick={() => saveToList(item)}>
                                Save To List
                            </Button>
                        </Card.Body>
                    </Card>
                    
                    <Card key={index} variant='outline' w='90%' mb={5}>
                        <Flex align="center" direction='row' mr='5'>
                            <Box p={5} py={2} minW='120px'  >
                            <Text textStyle='hanziText' mx={0} mt={3} >{item.hanzi}</Text>
                            <Text textStyle='pinyinText' mx={0} mt={-4}>{item.pinyin}</Text>
                            </Box>

                            <Divider orientation="vertical" borderColor="gray.500" height="80%" pr={2}/>

                            <Box p={4} >
                            <Text textStyle='translationText'  m='0'>{item.translation}</Text>
                            </Box>

                            <Spacer/> 

                            <IconButton boxSize={0} icon={<AddIcon/>} onClick={() =>{saveToList(item)}}/>

                        </Flex>

                    </Card>
                    </div>
                ))}
            </div> */}
            {/* <div style={{ position: 'fixed', bottom: '10%', left: '50%', transform: 'translate(-50%, 50%)', zIndex: 9999 }}>
            <Toast show={true} onClose={() => {}} delay={5000}>
                <Toast.Header>
                <strong className="me-auto">Saved</strong>
                <Button>Undo</Button>
                    
                </Toast.Header>
            </Toast>
            </div> */}
            </Flex>


       
     
    );
}