import { extendTheme } from '@chakra-ui/react'

const theme = extendTheme({
    styles: {
      global: {
        // styles for the `body`
        body: {
          fontFamily: `'Inter', sans-serif`
        },
      }
    },
    textStyles: {
      // Add your custom font sizes here
      heading: {
        fontSize:'24px',
        fontWeight:'bold',
      },
 
      hanziText:{
        fontSize:'28px',
        fontWeight:'500',
       
      },

      pinyinText:{
        fontSize:'14px',
        fontWeight:'400',
      
      },

      translationText:{
        fontSize:'14px',
        fontWeight:'400',
   
      },

      questionText:{
        fontSize:'64px',
        fontWeight:'500'
      }
 
    },
    components: {
        Button: {
          // 1. We can update the base styles
        //   baseStyle: {
        //     fontWeight: 'bold', // Normally, it is "semibold"
        //   },
          // 2. We can add a new button size or extend existing
          sizes: {
            md: {
              px: '44px',
              py: '20px',
              fontSize:'16px'
            },
          },
        },
    },
  });
  

export default theme